import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';

import { MessageService } from 'primeng/api';

import { DetalharService } from 'src/app/services/hall/detalhar/detalhar.service';
import { MapaService } from 'src/app/services/mapa/mapa.service';

@Component({
  selector: 'app-mapa-detail',
  templateUrl: './mapa-detail.component.html',
  styleUrls: ['./mapa-detail.component.scss'],
})
export class MapaDetailComponent implements OnInit {

  mapa: any;
  loading = true;

  constructor(
    private modal: ModalController,
    private modalInterno: ModalController,
    private detalharService: DetalharService,
    private mapaService: MapaService,
    private toast: MessageService,
  ) {

  }

  ngOnInit() {
    const id = this.detalharService.getId();

    this.mapaService.findById(id)
      .subscribe(res => {
        this.mapa = res;
        this.loading = false;
      },
        error => {
          console.log(error);
          this.loading = false;
        });
  }


  close() {
    this.modal.dismiss(null, null, "capitulo-detail");
  }

  mudaAba(event) {
  }

  callToast(severity, summary, detail) {
    this.toast.clear();
    this.toast.add({
      severity: severity,
      summary: summary,
      detail: detail
    });
  }


}
